import axios from 'axios'
import {bus} from '../bus.js'

axios.defaults.withCredentials = true;
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
 //axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';//

//
 axios.interceptors.request.use(function (config) {
  console.dir(config);
   return config;
 }, function (error) {
  // Do something with request error
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function (response) {
  if (response.data && response.data.errcode) {
    if (parseInt(response.data.errcode) === 40001) {
      //
      bus.$emit('goto', '/login')
    }
  }

  return response;
}, function (error) {
  // Do something with response error
  return Promise.reject(error);
});

//
let base = 'http://172.16.4.92:3000';  //config/index.jsproxyTable

//
export const POST = (url, params) => {
  return axios.post(`${base}${url}`, params).then(res => res.data)
}
let apiUrl="http://127.0.0.1:9000/";
export const POST_MY= (url, params) => {
  console.log(JSON.stringify(params)+"tijiaodeshuju0000000000000000");
  return axios.post(`${apiUrl}${url}`,{params:params}).then(res => res.data)
};

export const GET = (url, params) => {
  return axios.get(`${base}${url}`, {params: params}).then(res => res.data)
};

export const GET_MY= (url, params) => {
  console.log(`${apiUrl}${url}`+"");
  return axios.get(`${apiUrl}${url}`,{params: params}).then(res => res.data)
};

export const PUT = (url, params) => {
  return axios.put(`${base}${url}`, params).then(res => res.data)
}

export const DELETE = (url, params) => {
  return axios.delete(`${base}${url}`, {params: params}).then(res => res.data)
}

export const PATCH = (url, params) => {
  return axios.patch(`${base}${url}`, params).then(res => res.data)
}



