<template>
  <el-row class="warp">
    <!---->
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <!---->
    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.name" placeholder="" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="handleSearch"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table :data="books" highlight-current-row @selection-change="selsChange" style="width: 100%;">

        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column type="index" width="100"></el-table-column>
        <el-table-column prop="name" label="" width="250" sortable></el-table-column>
        <el-table-column prop="id" label="" width="250" sortable></el-table-column>
        <el-table-column prop="birthday" label="" width="250" sortable></el-table-column>
        <el-table-column prop="address" label=""  sortable></el-table-column>
        <el-table-column label="" width="250">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.$index,scope.row)"></el-button>
            <el-button type="danger" @click="delBook(scope.$index,scope.row)" size="small"></el-button>
          </template>
        </el-table-column>


      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-button type="danger" @click="batchDeleteBook" :disabled="this.sels.length===0"></el-button>
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>
      <el-dialog title="" :visible.sync ="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label="" prop="name">
            <el-input v-model="editForm.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="author">
            <el-input v-model="editForm.author" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker type="date" placeholder="" v-model="editForm.publishAt"></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input type="textarea" v-model="editForm.description" :rows="8"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false"></el-button>
          <el-button type="primary" @click.native="editSubmit"></el-button>
        </div>
      </el-dialog>
      <!---->
      <el-dialog title="" :visible.sync ="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
          <el-form-item label="" prop="name">
            <el-input v-model="addForm.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="id">
            <el-input v-model="addForm.id" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker type="date" placeholder="" v-model="addForm.birthday"></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input type="textarea" v-model="addForm.address" :rows="8"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false"></el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
        </div>
      </el-dialog>

    </el-col>
  </el-row>
</template>
<script type="text/ecmascript-6">/*es6js*/
  import util from '../../common/util'
  import API from '../../api/api_student_test';
  export default{
    data(){
      return {
        filters: {
          name: ''
        },
        books: [],
        total: 0,
        page: 1,
        limit: 10,
        loading: false,
        sels: [], //

        //
        editFormVisible: false,//
        editFormRules: {
          name: [
            {required: true, message: '', trigger: 'blur'}
          ],
          author: [
            {required: true, message: '', trigger: 'blur'}
          ],
          description: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        editForm: {
          id: 0,
          name: '',
          author: '',
          publishAt: '',
          description: ''
        },

        //
        addFormVisible: false,//
        addLoading: false,
        addFormRules: {
          name: [
            {required: true, message: '', trigger: 'blur'}
          ],
          id: [
            {required: true, message: '', trigger: 'blur'}
          ],
          birthday: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        addForm: {
          name: '',
          id: '',
          birthday: '',
          address: ''
        }
      }
    },
    methods: {
      handleCurrentChange(val) {
        this.page = val;
        this.search();
      },
      handleSearch(){
        this.total = 0;
        this.page = 1;
        this.search();
      },
      search(){
        let that = this;
        let params = {
          page: that.page,
          limit:10,
          name: that.filters.name
        };

        that.loading = true;
        API.findList(params).then(function (result) {
          that.loading = false;
          if (result && result.books) {
            that.total = result.total;
            console.log(result.books+"books192");
            that.books = result.books;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '', duration: 2000});
        });
      },
      selsChange: function (sels) {
        this.sels = sels;
      },
      //
      delBook: function (index, row) {
        let that = this;
        this.$confirm('?', '', {type: 'warning'}).then(() => {
          that.loading = true;
        API.remove(row.id).then(function (result) {
          that.loading = false;
          if (result && parseInt(result.errcode) === 0) {
            that.$message.success({showClose: true, message: '', duration: 1500});
            that.search();
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '', duration: 2000});
        });
      }).catch(() => {

        });
      },
      //
      showEditDialog: function (index, row) {
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //
      editSubmit: function () {
        let that = this;
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            let para = Object.assign({}, this.editForm);
            para.publishAt = (!para.publishAt || para.publishAt == '') ? '' : util.formatDate.format(new Date(para.publishAt), 'yyyy-MM-dd');
            API.update(para.id, para).then(function (result) {
              that.loading = false;
              if (result && parseInt(result.errcode) === 0) {
                that.$message.success({showClose: true, message: '', duration: 2000});
                that.$refs['editForm'].resetFields();
                that.editFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '', duration: 2000});
            });
          }
        });
      },
      showAddDialog: function () {
        this.addFormVisible = true;
        this.addForm = {
          name: '',
          id: '',
          birthday: '',
          address: ''
        };
      },
      //
      addSubmit: function (){
        let that = this;
        this.$refs.addForm.validate((valid) =>{
          if (valid) {
            that.loading = true;
            let para = Object.assign({}, this.addForm);
            console.log(JSON.stringify(para)+"69696969");
            para.birthday = (!para.birthday || para.birthday === '') ? '' : util.formatDate.format(new Date(para.birthday), 'yyyy-MM-dd');
            API.add(para).then(function (result) {  //
              that.loading = false;
              console.log(JSON.stringify(result)+"9999");
              if (result && parseInt(result.code) === 0) {
                that.$message.success({showClose: true, message: '', duration: 2000});
                that.$refs['addForm'].resetFields();
                that.addFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '', duration: 2000});
            });

          }
        });
      },
      //
      batchDeleteBook: function () {
        let ids = this.sels.map(item => item.id).toString();
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          that.loading = true;
        API.removeBatch(ids).then(function (result) {
          that.loading = false;
          if (result && parseInt(result.errcode) === 0) {
            that.$message.success({showClose: true, message: '', duration: 1500});
            that.search();
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '', duration: 2000});
        });
      }).catch(() => {

        });
      }
    },
    mounted() {
      this.handleSearch()
    }
  }



</script>



