import * as API from './'

export default {

  //book(page)
  findList: params => {
    console.log(API.GET_MY('getAll', params));
    return API.GET_MY('getAll', params);
  },
  //book
  findById: id => {
    return API.GET(`/api/v1/books/${id}`)
  },

  add: params => {
    return API.POST_MY("Insert",params)
  },
  update: (id, params) => {
    return API.PUT(`/api/v1/books/${id}`, params)
  },

  //book
  remove: id => {
    return API.DELETE(`/api/v1/books/${id}`)
  },

  //ids
  removeBatch: (ids) => {
    return API.DELETE(`/api/v1/books/batch/${ids}`)
  }

}



